import { styled } from "@rp/react/theme"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, Button, alpha } from "@rp/react/web/mui/core"

const PREFIX = "StoryCard"

export const classes = {
  storyCardRoot: `${PREFIX}-storyCardRoot`,
  headlineContainer: `${PREFIX}-headlineContainer`,
  actionsContainer: `${PREFIX}-actionsContainer`,
}

type StoryCardProps = {
  showCluster?: boolean | undefined
  isVisited?: boolean | undefined
}

export const StoryCardStyled = styled("div", {
  name: "StoryCardStyled",
  shouldForwardProp: prop => !["showCluster", "isVisited"].includes(prop.toString()),
})<StoryCardProps>(({ theme, showCluster, isVisited }) => {
  const { isMobile } = useDeviceInfo()
  const CLUSTER_BG_COLOR = theme.palette.primary.background || ""

  return {
    position: "relative",
    padding: theme.spacing(1.5),
    marginLeft: 0,
    marginRight: 0,
    transition: "background 0.3s",
    cursor: "pointer",
    display: "block",
    borderRadius: isMobile ? 0 : 8,
    backgroundColor: isMobile ? theme.palette.elevation.dp01 : "transparent",

    "&:hover": isMobile
      ? undefined
      : {
          background: theme.palette.elevation.dp08,
          backgroundColor: showCluster ? alpha(CLUSTER_BG_COLOR, 0.18) : undefined,
          [`& .${classes.actionsContainer}`]: {
            visibility: "visible",
            "& button": {
              color: theme.palette.text.secondary,
            },
          },
        },

    "&": showCluster ? { backgroundColor: alpha(CLUSTER_BG_COLOR, 0.12) } : undefined,

    "&:focus": {
      background: theme.palette.grey[500],
      border: `1px solid white`,
    },

    "&:active": {
      background: theme.palette.grey[700],
    },

    [`& .${classes.headlineContainer}`]: {
      maxWidth: "100%",
      whiteSpace: isMobile ? undefined : "nowrap",
      display: isMobile ? undefined : "flex",
      alignItems: isMobile ? undefined : "center",
      color: isVisited ? theme.palette.visited : theme.palette.blue[200],
    },
  }
})

const RelatedDocumentButton = styled(Button)(({ theme }) => {
  const { isMobile } = useDeviceInfo()
  return {
    position: "relative",
    left: isMobile ? theme.spacing(2) : `-${theme.spacing(1.5)}`,
    padding: 0,
    color: alpha("#fff", 0.6),
    textTransform: "none",
    margin: "12px 0",
    "&:hover": {
      backgroundColor: "transparent",
    },
    "& svg": {
      verticalAlign: "middle",
    },
  }
})

const StoryCardClusterStyled = styled(Box)(({ theme }) => {
  const { isMobile } = useDeviceInfo()
  return {
    display: "block",
    "& > div": {
      padding: isMobile ? `0 ${theme.spacing(2)}` : "0 0 0 30px",
    },
    [`& div.${classes.storyCardRoot}`]: {
      borderRadius: "8px",
      backgroundColor: isMobile
        ? alpha(theme.palette.white, 0.08)
        : alpha(theme.palette.white, 0.05),
    },
    [`& div.${classes.storyCardRoot}:hover`]: {
      backgroundColor: isMobile ? undefined : alpha(theme.palette.white, 0.08),
    },
  }
})

const StoryCardHeaderInfo = styled(Box)(({ theme }) => ({
  display: "flex",
  alignItems: "center",
  fontSize: 12,
  marginBottom: 4,
  color: theme.palette.grey[300],
  "& > button": {
    fontFamily: theme.typography.fontFamily,
    color: theme.palette.primary.light,
  },
}))

const StoryCardStory = styled(Box)(({ theme }) => {
  const { isMobile } = useDeviceInfo()
  return {
    color: theme.palette.text.secondary,
    overflowY: "hidden",
    display: "-webkit-box",
    overflowWrap: "break-word",
    WebkitBoxOrient: "vertical",
    WebkitLineClamp: isMobile ? 5 : 3,
    textAlign: "start",
    fontSize: theme.typography.body1.fontSize,
    "& p": {
      margin: 0,
    },
  }
})

const StoryCardHeader = styled(Box)(({ theme }) => {
  const { isMobile } = useDeviceInfo()

  return {
    position: "relative",
    display: "flex",
    alignItems: "end",

    // headline
    "& a": {
      flexShrink: 2,
      fontSize: "18px",
      textAlign: "start",
      // Desktop ellipsis
      whiteSpace: isMobile ? undefined : "nowrap",
      textOverflow: isMobile ? undefined : "ellipsis",
      overflow: isMobile ? undefined : "hidden",
      // Mobile ellipsis
      overflowY: isMobile ? "hidden" : undefined,
      display: isMobile ? "-webkit-box" : undefined,
      overflowWrap: isMobile ? "break-word" : undefined,
      WebkitBoxOrient: isMobile ? "vertical" : undefined,
      WebkitLineClamp: isMobile ? 2 : undefined,
      // Anchor styles
      color: "inherit",
      "&:hover": {
        textDecoration: "none",
      },
      "&.visited": {
        color: theme.palette.visited,
      },
    },
  }
})

export {
  StoryCardClusterStyled,
  RelatedDocumentButton,
  StoryCardHeaderInfo,
  StoryCardStory,
  StoryCardHeader,
}
